@charset "UTF-8";
.content {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center; }

.item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  flex: 1;
  min-width: 380px;
  border-radius: 10px;
  margin: 5px;
  padding: 20px;
  background-color: rgba(15, 15, 15, 0.02);
  color: #55c500;
  text-decoration: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s; }

.item:hover {
  background-color: #55c500;
  color: white;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }
  .item:hover .vartical {
    background-color: white; }

.avatorImg {
  margin-right: 20px; }

.itemText {
  width: 90%;
  font-size: 20px;
  font-family: Quicksand, 游ゴシック体, "Yu Gothic", YuGothic, "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", メイリオ, Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif; }

.likesCount {
  min-width: 70px;
  font-size: 14px;
  font-family: Quicksand, 游ゴシック体, "Yu Gothic", YuGothic, "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", メイリオ, Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif; }

.vartical {
  background-color: #55c500;
  border: 0px;
  min-width: 1px;
  height: 100%;
  padding: 0;
  margin: 0 20px; }
